.list {
    display: flex;
    flex-direction: column;

    background-color: var(--tg-theme-bg-color);

    border-radius: 6px;
    padding-left: 15px;

}

.btn {
    background-color: transparent;
    position: relative;
    font-size: 16px;
    border: none;
    text-align: left;
    padding: 10px;
    padding-left: 0;
    border-bottom: 1px solid var(--tg-theme-secondary-bg-color);
    color: var(--tg-theme-text-color);
}

.btn:first-child {
    border-top-left-radius: 6px;
    /* Применяем радиус к верхнему левому углу первого элемента */
    border-top-right-radius: 6px;
    /* Применяем радиус к верхнему правому углу первого элемента */
}

.btn:last-child {
    border-bottom-left-radius: 6px;
    /* Применяем радиус к нижнему левому углу последнего элемента */
    border-bottom-right-radius: 6px;
    /* Применяем радиус к нижнему правому углу последнего элемента */
    border: none;

}

.icon {
    position: absolute;
    right: 15px;
    color: var(--tg-theme-button-color);
}